<template>
  <div class="pay">
    <!-- 顶部 -->
    <div class="pay_top">
      <van-row>
        <van-col span="10">
          <van-icon name="arrow-left" class="icon" size="24" @click="upperLevel" color="#fff" />
        </van-col>
        <van-col span="14">
          <h3>支付详情</h3>
        </van-col>
      </van-row>
    </div>
    <!-- 顶部 end -->

    <!-- 选择地址 -->
    <div class="address" @click="showActionSheet">
      {{addressData.address}}
      <van-icon name="arrow" class="fr icon" />
    </div>
    <!-- 选择地址 end -->

    <!-- 支付内容 -->
    <div class="pay_list">
      <van-divider />
      <div class="list">
        <div class="name clearfix" v-for="(item,index) in orderData" :key="index">
          <div class="img fl">
            <img :src="item.small_img" alt />
          </div>
          <div class="txt">
            <div class="noe">
              <p class="title">{{item.name}}</p>
              <p class="price fr">￥{{item.price}}</p>
            </div>
            <p class="introduction">{{item.enname}}</p>
            <div class="txt">
              <div class="noe">
                <p class="specification">{{item.rule}}</p>
                <p class="num fr">×{{item.count}}</p>
              </div>
            </div>
          </div>
        </div>
      </div>

      <van-divider />
      <p class="total">
        共计{{orderDesc.count}}件商品，合计：￥
        <span>{{orderDesc.total}}</span>
      </p>
    </div>
    <!-- 支付内容 end -->

    <!-- 弹出式/收货地址 -->
    <van-action-sheet v-model="isShow" title="选择收货地址">
      <van-address-list
        v-model="chosenAddressId"
        :list="list"
        default-tag-text="默认"
        :switchable="true"
        @select="selectAddress"
        @add="newAddress"
      ></van-address-list>
    </van-action-sheet>
    <!-- 弹出式/收货地址 end-->

    <van-submit-bar
      :price="orderDesc.total * 100"
      button-type="info"
      button-text="提交订单"
      @submit="pay"
    />
  </div>
</template>

<script>
import { utils } from "../utils/utils";

import { mapState } from "vuex";

export default {
  name: "Pay",
  data() {
    return {
      isShow: false,
      chosenAddressId: "0"
    };
  },

  computed: {
    ...mapState("payModule", [
      "orderData",
      "orderDesc",
      "isSelect",
      "list",
      "addressData",
      "sids"
    ])
  },
  created() {
    this.$store.state.vanTabbar = false;

    this.$store.commit("payModule/emptyData");

    //截取查询查询参数
    let sids = this.$route.query.sids.split("-");

    this.$store.commit("payModule/changeData", { key: "sids", value: sids });

    this.findBuyProduct(sids);

    this.getAddress();
  },
  // 生命周期钩子，最后销毁函数
  destroyed() {
    // 当组件被销毁时候，修改导航数据
    this.$store.state.vanTabbar = true;
  },

  methods: {
    // 返回上一级
    upperLevel() {
      this.$router.go(-1);
    },

    // 控制弹出
    showActionSheet() {
      this.isShow = true;
    },

    //根据sid查询需要购买的商品
    findBuyProduct(sids) {
      let tokenString = localStorage.getItem("_t");
      //加载提示
      this.$toast.loading({
        forbidClick: true,
        duration: 0,
        message: "加载中..."
      });
      this.axios({
        type: "GET",
        url: "/commitShopcart",
        params: {
          appkey: this.appkey,
          tokenString,
          sids: JSON.stringify(sids)
        }
      })
        .then(result => {
          this.$toast.clear();

          this.orderDesc.count = 0;
          this.orderDesc.total = 0;

          result.data.result.forEach(v => {
            this.orderDesc.count += v.count;

            this.orderDesc.total += v.count * v.price;
          });

          this.$store.commit("payModule/changeData", {
            key: "orderData",
            value: result.data.result
          });
        })
        .catch(err => {
          this.$toast.clear();
        });
    },

    //获取收货地址
    getAddress() {
      let tokenString = localStorage.getItem("_t");
      this.axios({
        method: "GET",
        url: "/findAddress",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          let list = [];
          result.data.result.forEach((v, i) => {
            let address = {
              id: i + "",
              name: v.name,
              tel: v.tel,
              address: v.province + v.city + v.county + v.addressDetail
            };

            list.push(address);

            if (v.isDefault == 1) {
              this.$store.commit("payModule/changeData", {
                key: "isSelect",
                value: true
              });
              // this.$store.commit("payModule/changeData", {
              //   key: "address",
              //   value: address.address
              // });
              this.addressData.address = address.address;
              this.chosenAddressId = i + "";
            }
          });

          this.$store.commit("payModule/changeData", {
            key: "list",
            value: list
          });
        })
        .catch(err => {});
    },

    //选择地址
    selectAddress(item, index) {
      if (!this.isSelect) {
        this.$store.commit("payModule/changeData", {
          key: "isSelect",
          value: true
        });
      }
      // this.$store.commit("payModule/changeData", {
      //   key: "address",
      //   value: item.address
      // });

      this.addressData.phone = item.tel;
      this.addressData.receiver = item.name;
      this.addressData.address = item.address;

      this.isShow = false;
    },

    //新增地址
    newAddress() {
      this.$router.push({ name: "NewAddress", query: { isAdd: 1 } });
    },

    //结算
    pay() {
      if (!this.isSelect) {
        this.$toast(this.address);
        return;
      }

      let tokenString = localStorage.getItem("_t");

      let sids = this.sids.concat();

      //加载提示
      this.$toast.loading({
        forbidClick: true,
        duration: 0,
        message: "加载中..."
      });

      this.axios({
        method: "POST",
        url: "/pay",
        data: {
          appkey: this.appkey,
          tokenString,
          sids: JSON.stringify(sids),
          phone: this.addressData.phone,
          receiver: this.addressData.receiver,
          address: this.addressData.address
        },
        transformRequest: utils.transformRequest
      })
        .then(result => {
          this.$toast.clear();

          this.$router.push({ name: "Order" });
        })
        .catch(err => {
          this.$toast.clear();

        });
    }
  }
};
</script>

<style lang="less" scoped>
.van-divider {
  margin: 8px 0;
}
.pay {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: #f9f9f9;
  margin-bottom: 50px;
  .pay_top {
    background-color: #0197fe;
    .icon {
      line-height: 40px;
    }
    h3 {
      line-height: 40px;
      color: #fff;
      font-weight: 400;
    }
  }
  .address {
    padding: 0px 20px;
    color: #333;
    margin: 10px;
    background-color: #fff;
    line-height: 40px;
    font-size: 15px;
    border-radius: 5px;
    .icon {
      line-height: 40px;
    }
  }
  .pay_list {
    margin: 0 10px;
    .list {
      background-color: #fff;
      padding: 10px;

      .name {
        margin: 10px 0;
        .img {
          img {
            width: 90px;
            height: 90px;
            border-radius: 8px;
          }
        }
        .txt {
          padding: 0px 10px;
          text-indent: 10px;
          .noe {
            padding: 10px 0px;
            .title {
              color: #333;
              font-weight: 700;
              display: inline;
            }
            .price {
              font-size: 14px;
            }
            .specification {
              font-size: 14px;
              display: inline;
            }
          }
          .introduction {
            color: #999;
            font-size: 14px;
          }
          .num {
            font-size: 14px;
          }
        }
      }
    }
    .total {
      font-size: 13px;
      text-align: right;
      span {
        color: #333;
        font-weight: 700;
        font-size: 18px;
      }
    }
    .time {
      color: #999;
      font-size: 13px;
    }
  }
}

/deep/ .van-address-item__edit {
  color: #fff;
}

/deep/ .van-button--info {
  background-color: #36c970;
}

/deep/ .van-button--danger{
  background-color: #36C970;
}

/deep/ .van-address-item .van-radio__icon--checked .van-icon{
    background-color: #36C970;
}
</style>